let cityField = $("._city-field")

$("._got-to-step1").click(function () {
    setStep(1)
})
$("._got-to-step2").click(function () {
    setStep(2)
})
$("._got-to-step3").click(function () {
    setStep(3)
})
$("._got-to-step4").click(function () {
    setStep(4)
})

function setStep(id){
    $(".calc-step").removeClass("_active")
    $(`.calc-step[data-step="${id}"]`).addClass("_active")
    $(`.calc-progress__item[data-step="${id}"]`).addClass("_filled")
}

$(cityField).on("input",function () {
    if($(this).val() !== ""){
        $("._got-to-step2.m-btn-green").prop("disabled",false)
    } else{
        $("._got-to-step2.m-btn-green").prop("disabled",true)
    }
})

$(".calc-cities__item").click(function () {
    let val = $(this).find("span").text()
    $(cityField).val(val)
    $(cityField).trigger('input');
})

$(`[data-name="certificates"]`).change(function () {
    if($(`[data-name="certificates"]:checked`).length !== 0){
        $("._got-to-step3.m-btn-green").prop("disabled",false)
        let sum = 0

        $(`[data-name="certificates"]:checked`).each(function (i,el) {
            sum += +$(el).attr("data-value")
        })


        $("._sum").html(`${sum.toLocaleString()} рублей`)
    }else{
        $("._got-to-step3.m-btn-green").prop("disabled",true)
        $("._sum").html("0 рублей")
    }
})

$(`[data-name="useFor"]`).change(function () {
    if($(`[data-name="useFor"]:checked`).length !== 0){
        $("._got-to-step4.m-btn-green").prop("disabled",false)
    }else{
        $("._got-to-step4.m-btn-green").prop("disabled",true)
    }
})


